import React from "react";
import Button from "../UI/Button/Button";
import classes from "./TodoControl.module.css";

const TodoControl = (props) => {
   const activeSortButtonClasses = [
      classes.sortItem,
      classes.sortItemActive,
   ].join(" ");
   return (
      <>
         <div className={classes.TodoControl}>
            <p className={classes.completed}>
               Выполнено: {props.completed} из {props.length}
            </p>
            <p className={classes.left}>
               Осталось сделать: {props.length - props.completed}
            </p>
         </div>
         <div className={classes.TodoControl}>
            <div className={classes.sort}>
               {props.sortButtons
                  ? props.sortButtons.map((button) => {
                       return (
                          <Button
                             key={button.id}
                             type={
                                props.activeSortButton === button.id
                                   ? activeSortButtonClasses
                                   : classes.sortItem
                             }
                             onClick={props.onSortButtonClickHandler.bind(
                                this,
                                button.id,
                                button.sortParam
                             )}
                          >
                             {button.text}
                          </Button>
                       );
                    })
                  : null}
            </div>
         </div>
      </>
   );
};

export default TodoControl;
